
import { RoundService } from "@/services/RoundService";
import { DISABLE_FULL_SCREEN } from "@/store/modules/UIModule";
import { Tick } from "@/model/Tick";
import { Options, Vue } from "vue-class-component";

@Options({
  data() {
    return {
      // audio: null,
      roundService: new RoundService(), //TODO - Ne viene generato uno nuovo a ogni round?
    };
  },
  computed: {
    bombValue() {
      return this.$store.state.round.bombTime;
    },
  },
  /* watch: {
    ['$store.state.round.started'](newS, oldS) {
        console.log(oldS + ' -> ' + newS);
    }
  }, */
  mounted() {
    const audio = new Audio("Explosion.mp3");
    /* audio.loop = true;Ê
    audio.play(); */
    const tick = new Tick();
    tick.start();
    //TODO - Spostare fuori??
    setTimeout(async () => {
      tick.stop();
      // audio.pause();
      audio.loop = false;
      // audio.src = "Explosion.mp3";
      audio.play();

      if ("vibrate" in navigator) {
        (navigator as Navigator).vibrate(900);
      }

      this.roundService.onRoundEnd();
      this.$store.commit(DISABLE_FULL_SCREEN);
    }, this.$store.state.round.bombTime * 1000);
  },
})
export default class Bomb extends Vue {}
