
import { Options, Vue } from "vue-class-component";
import { DiceValue } from "../model/DiceValue";

@Options({
  data() {
    return {};
  },
  computed: {
    roundDiceValue() {
      return this.$store.state.round.selectedDiceValue;
    },
    roundSyllable() {
      return this.$store.state.round.selectedSyllable;
    },
    diceSentence() {
      let sentence;
      switch (DiceValue[this.roundDiceValue] as unknown as DiceValue) {
        case DiceValue.START:
          sentence = ["all'inizio", "della"];
          break;
        case DiceValue.END:
          sentence = ["alla fine", "della"];
          break;

        default:
          sentence = ["ovunque", "nella"];
          break;
      }
      return sentence;
    },
  },
})
export default class Dice extends Vue {}
