import { DiceValue } from "@/model/DiceValue";
import { Module } from "vuex";
import { RootState } from "..";

export interface Round {
    selectedSyllable: string | null,
    selectedDiceValue: DiceValue | null,
    bombTime: number | null,
    started: boolean
}

export const roundModule: Module<Round, RootState> = {
    state: {
        selectedSyllable: null,
        selectedDiceValue: null,
        bombTime: null,
        started: false
    },
    mutations: {
        changeSelectedSyllable(state, syllabe: string) {
            state.selectedSyllable = syllabe;
        },
        changeSelectedDiceValue(state, diceValue: DiceValue) {
            state.selectedDiceValue = diceValue;
        },
        changeBombTime(state, bombTime: number) {
            state.bombTime = bombTime;
        },
        stopRound(state) {
            state.started = false;
        },
        startRound(state) {
            state.started = true;
        },
    },
}

export const
    CHANGE_SELECTED_SYLLABLE = 'changeSelectedSyllable',
    CHANGE_SELECTED_DICE_VALUE = 'changeSelectedDiceValue',
    CHANGE_BOMB_TIME = 'changeBombTime',
    START_ROUND = 'startRound',
    STOP_ROUND = 'stopRound'