
import {
  CHANGE_MAX_BOMB_TIME,
  CHANGE_MIN_BOMB_TIME,
  TOGGLE_FONT,
  TOGGLE_FULL_SCREEN,
} from "@/store/modules/SettingsModule";
import { Vue, Options } from "vue-class-component";

@Options({
  data() {
    const showMenu = false;
    return { showMenu };
  },
  methods: {
    onClickOutside(/* _e: MouseEvent, _el: Event */) {
      this.showMenu = false;
    },
    toggleFont() {
      this.$store.commit(TOGGLE_FONT);
    },
    toggleFullScreen() {
      this.$store.commit(TOGGLE_FULL_SCREEN);
    },
    updateMinTime(event: Event) {
      if (event.target) {
        let t = parseInt((event.target as HTMLInputElement).value);
        if (t >= this.maxBombTime) {
          // alert("Il tempo minimo deve essere minore del tempo massimo!");
          t = this.maxBombTime - 20;
        }
        this.$store.commit(CHANGE_MIN_BOMB_TIME, t);
      }
    },
    updateMaxTime(event: Event) {
      if (event.target) {
        let t = parseInt((event.target as HTMLInputElement).value);
        if (t < this.minBombTime) {
          // alert("Il tempo massimo deve essere maggiore del tempo minimo!");
          t = this.minBombTime + 20;
        }
        this.$store.commit(CHANGE_MAX_BOMB_TIME, t);
      }
    },
  },
  computed: {
    minBombTime() {
      return this.$store.state.settings.minBombTime;
    },
    maxBombTime() {
      return this.$store.state.settings.maxBombTime;
    },
    readableFont() {
      return this.$store.state.settings.font === "Avenir";
    },
    fullScreenEnabled() {
      return this.$store.state.settings.fullScreen;
    },
  },
})
export default class Settings extends Vue {}
