import { InjectionKey } from 'vue';
import { createStore, Store } from 'vuex'
import { Round, roundModule } from './modules/RoundModule';
import { Settings, settingsModule } from './modules/SettingsModule';
import { UI, uiModule } from './modules/UIModule';

export interface RootState {
  ui: UI,
  round: Round,
  settings: Settings
}

export const key: InjectionKey<Store<RootState>> = Symbol();

export const store = createStore<RootState>({
  modules: {
    ui: uiModule,
    round: roundModule,
    settings: settingsModule
  }
})
