import { DiceValue } from "@/model/DiceValue";
import { syllabes } from "@/model/Syllables";
import { key } from "@/store";
import { CHANGE_BOMB_TIME, CHANGE_SELECTED_DICE_VALUE, CHANGE_SELECTED_SYLLABLE, START_ROUND, STOP_ROUND } from "@/store/modules/RoundModule";
import { useStore } from "vuex";

export class RoundService {
    readonly store = useStore(key);
    readonly syllablesList = syllabes;

    onNewRound(): void {
        const maxT = this.store.state.settings.maxBombTime;
        const minT = this.store.state.settings.minBombTime;
        const sill = this.syllablesList[Math.floor(Math.random() * (this.syllablesList.length))];
        const diceV = DiceValue[Math.floor(Math.random() * 3)];
        const bombTime = Math.floor(Math.random() * (maxT - minT)) + minT;
        this.store.commit(CHANGE_SELECTED_DICE_VALUE, diceV);
        this.store.commit(CHANGE_SELECTED_SYLLABLE, sill);
        this.store.commit(CHANGE_BOMB_TIME, bombTime);
        this.store.commit(START_ROUND);
    }
    
    onRoundEnd(): void {
        this.store.commit(STOP_ROUND);
    }
}