import { Module } from "vuex";
import { RootState } from "..";

export interface UI {
    fullScreen: boolean
}

export const uiModule: Module<UI, RootState> = {
    state: {
        fullScreen: false
    },
    mutations: {
        enableFullScreen(state) {
            state.fullScreen = true;
        },
        disableFullScreen(state) {
            state.fullScreen = false;
        }
    },
}

export const
    ENABLE_FULL_SCREEN = 'enableFullScreen',
    DISABLE_FULL_SCREEN = 'disableFullScreen'
