import { Module } from "vuex";
import { RootState } from "..";

export interface Settings {
    maxBombTime: number,
    minBombTime: number,
    font: 'Helvetica' | 'Cabin Sketch' | string,
    fullScreen: boolean
}

export const settingsModule: Module<Settings, RootState> = {
    state: {
        maxBombTime: 60,
        minBombTime: 10,
        font: 'Cabin Sketch',
        fullScreen: true,
    },
    mutations: {
        changeMinBombTime(state, minBombTime: number) {
            state.minBombTime = minBombTime;
        },
        changeMaxBombTime(state, maxBombTime: number) {
            state.maxBombTime = maxBombTime;
        },
        toggleFont(state, font) {
            if (font != null) {
                state.font = font;
            } else {
                state.font = state.font === 'Avenir' ? 'Cabin Sketch' : 'Avenir';
            }
        },
        toggleFullScreen(state, enableFullScreen) {
            if (enableFullScreen != null) {
                state.fullScreen = enableFullScreen;
            } else {
                state.fullScreen = !state.fullScreen;
            }
        },
    },
}

export const
    CHANGE_MIN_BOMB_TIME = 'changeMinBombTime',
    CHANGE_MAX_BOMB_TIME = 'changeMaxBombTime',
    TOGGLE_FONT = 'toggleFont',
    TOGGLE_FULL_SCREEN = 'toggleFullScreen'
