
import { Options, Vue } from "vue-class-component";
import Round from "../components/Round.vue";
import Bomb from "../components/Bomb.vue";
import NewRound from "../components/NewRound.vue";
import Settings from "../components/Settings.vue";

interface Navigator {
  wakeLock: {
    request: (s: string) => Promise<boolean>;
  };
}

@Options({
  components: {
    Round,
    Bomb,
    NewRound,
    Settings,
  },
  data() {
    return {
      wakeLock: null,
      firstRound: true
    };
  },
  computed: {
    roundStarted() {
      return this.$store.state.round.started;
    },
  },
  watch: {
    async ["$store.state.ui.fullScreen"](newS, oldS) {
      if (newS && !oldS) {
        try {
          if (this.$store.state.settings.fullScreen) {
            await this.$el.requestFullscreen();
          }
          this.wakeLock = await (
            navigator as unknown as Navigator
          ).wakeLock.request("screen");

          this.firstRound = false; // TODO - è inutire riassegnarla ogni volta
        } catch (error) {
          console.error(error);
        }
      } else {
        try {
          if (
            document.fullscreenElement /* && this.$store.state.settings.fullScreen */
          ) {
            await document.exitFullscreen();
          }
          if (this.wakeLock) {
            await this.wakeLock.release();
          }
        } catch (error) {
          console.error(error);
        }
      }
    },
  },
})
export default class HomePage extends Vue {}
